.color-button-list {
    text-align: center;
}

.color-button {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: inline-block;
    cursor: pointer;
    border: 4px solid transparent;
}

.color-button.selected {
    border-color: white;
}

.color-button + .color-button {
    margin-left: .5rem;
}