body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 20px;
  background: #401A89;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.player-code {
  padding: 1rem;
}

input {
  padding: 0.5em;
}

input+button {
  margin-left: .5rem;
}

button {
  cursor: pointer;
  font-size: .875rem;
  padding: 0.5em 1.5em;
  border-radius: 0.25em;
  background-color: #26890C;
  color: #fff;
  font-weight: 700;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-decoration: none;
  display: inline-block;
  position: relative;
  top: 0;
  position: relative;
  min-width: 6rem;
  text-align: center;
  -webkit-transition: .2s all;
  -o-transition: .2s all;
  transition: .2s all;
  text-decoration: none;
  outline: 0;
  border-width: 0;
}

button:disabled:not(.selected) {
  background-color: #999;
  cursor: not-allowed;
}

button::before {
  content: ' ';
  position: absolute;
  left: 0;
  width: 100%;
  top: 0;
  bottom: -0.25em;
  background-color: inherit;
  z-index: -1;
  border-radius: 0.25em;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

button::after {
  content: ' ';
  position: absolute;
  left: 0;
  width: 100%;
  top: 0;
  bottom: -0.25em;
  background-color: inherit;
  z-index: -1;
  border-radius: 0.25em;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  background: rgba(0, 0, 0, 0.25);
}

button:hover {
  top: 0.125em;
}

button:hover::before {
  bottom: -0.125em;
}

button:hover::after {
  bottom: -0.125em;
}

.button--big {
  font-size: 2rem;
}

.card {
  background-color: #efefef;
  color: #000;
  padding: 1rem;
  border-radius: .5rem;
  display: inline-block;
  margin-right: 1rem;
  margin-bottom: 1rem;
}

.card-list {
  margin: .5rem 0;
}

#root,
.fullscreen,
body,
html {
  width: 100%;
  height: 100%;
}

#root {
  box-sizing: border-box;
  padding: 1rem;
}

.red {
  background-color: #E21B3C;
}

.blue {
  background-color: #1368CE;
}

.orange {
  background-color: #FFAB10;
}

.green {
  background-color: #26890C;
}

.pink {
  background-color: yellowgreen;
}

.violet {
  background-color: violet;
}

.centered {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.versus {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 0 10%;
  box-sizing: border-box;
  align-items: center;
  margin-top: 1rem;
}

.versus__team {
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
}

.versus__team.selected {
  position: relative;
}

.versus__team.selected::before {
  content: 'â†‘';
  font-weight: bold;
  position: absolute;
  bottom: -150%;
  left: 50%;
  transform: translate(-50%, 0);
  animation: bounce2 2s ease infinite;
}

.instructions {
  font-size: 2rem;
}

.versus__versus {
  flex: 1;
  font-weight: bold;
  align-items: center;
  font-size: 5rem;
  text-align: center;
}

.bounce2 {
  animation: bounce2 1s linear infinite;
}

@keyframes bounce2 {

  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-50px);
  }
}

.answer-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
}

.answer-list>button {
  font-size: 2rem;
  margin: 10px;
  flex: 1 1 40%;
}


@media only screen and (max-width: 1000px) {
  .button--answer, .answer-list > button {
    font-size: 1rem;
  }
}