.voteBar {
    width: 400px;
    height: 100px;
    border: 10px solid #999;
    border-radius: 1rem;
    background-color: #ccc;
    position: relative;
}


.progress {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: #26890C;
    transition: width 300ms ease-in;
}